.movie_container {
    background-color: black;
    padding: 2%;
  }
  
  h3 {
    padding-bottom: 2%;
  }
  
  @media only screen and (max-width: 600px) {
    h3 {
      padding-bottom: 12%;
      font-size: 11vw;
      color: whitesmoke;
    }
    .movie_container {
      display: flex;
      flex-direction: column;
      align-items: center;
      height: 115vw;
    }
  }
  