.movieDetails_body {
  display: flex;
  flex-direction: row;
  background-color: #282828;
  height: auto;
}

.youtube-player {
  display: flex;
  flex-direction: column;
  flex: 0.48;
  height: auto;
}

.player-wrapper {
  position: relative;
  padding-top: 56.25%; /* Player ratio: 100 / (1280 / 720) */
  margin-top: 0.1%;
  margin-left: 0.3%;
}

.react-player {
  position: absolute;
  top: 0;
  left: 0;
}

.movie_details {
  display: flex;
  flex-direction: column;
  padding-left: 1%;
  flex: 0.52;
  overflow-y: auto;
  height: 350px;
}

.title {
  font-size: 3vw;
  color: #fff;
}
.details {
  color: white;
}

.details_items {
  font-size: 2vw;
}

.tagline {
  font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
  font-size: 3vw;
  color: grey;
}

.overview {
  color: #fff;
  font-size: 1vw;
}


@media only screen and (max-width: 600px) {
  .movieDetails_body {
    display: flex;
    flex-direction: column;
    background-color: #282828;
    height: auto;
    overflow: hidden;
  }

  .youtube-player {
    display: flex;
  }

  .movie_details {
    display: flex;
    padding: 5%;
  }
  .title {
    font-size: 10vw;
  }

  .details {
    color: white;
  }

  .details_items {
    font-size: 5vw;
  }
  .tagline {
    font-family: Cambria, Cochin, Georgia, Times, "Times New Roman", serif;
    font-size: 9vw;
    color: grey;
  }

  .overview {
    font-size: 5vw;
  }
}
